<template>
  <div>
    <!-- 메인 화면 -->
    <div v-if="currentView === 'main'">
      <div style="padding:10px; border-bottom:1px solid #ccc;">
        <span style="font-weight:bold;">LOGO</span>
        <input placeholder="검색어를 입력하세요" style="margin-left:20px; width:300px;" />
      </div>

      <div style="padding:10px; border-bottom:1px solid #ccc;">
        <span>홈 | 베스트 | 신상품</span>
        <button @click="searchSimilar" style="margin-left:20px;">유사이미지</button>
        <button @click="compare" style="margin-left:10px;">비교하기</button>
      </div>

      <div style="display:flex;">
        <div style="width:150px; border-right:1px solid #ccc; padding:10px;">
          <div>기저귀</div>
          <div>분유</div>
          <div>이유식</div>
        </div>

        <div style="display:flex; flex-wrap:wrap; padding:10px;">
          <div v-for="p in products" :key="p.pcode" style="border:1px solid #ddd; padding:10px; margin:5px; width:180px;">
            <input type="checkbox" :value="p.pcode" v-model="selected" :disabled="selected.length >= 2 && !selected.includes(p.pcode)" />
            <img :src="`http://localhost:8083/images/${p.image}`" style="width:100%; height:120px; object-fit:cover;" />
            <div>{{ p.productName }}</div>
            <div>{{ p.priceMin?.toLocaleString() || '-' }}원</div>
          </div>
        </div>
      </div>
    </div>

    <!-- 비교 결과 화면 -->
    <div v-else-if="currentView === 'compare'" style="padding:20px;">
      <button @click="goBack">← 뒤로가기</button>
      <h2>상품 비교 결과</h2>
      <div style="border:1px solid #ddd; padding:20px; margin-top:10px;">
        <div v-if="loading">분석 중...</div>
        <div v-else style="white-space:pre-wrap;">{{ compareResult }}</div>
      </div>
    </div>

    <!-- 유사 이미지 결과 화면 -->
    <div v-else-if="currentView === 'similar'" style="padding:20px;">
      <button @click="goBack">← 뒤로가기</button>
      <h2>유사 이미지 검색 결과</h2>
      <div style="border:1px solid #ddd; padding:20px; margin-top:10px;">
        <div v-if="loading">검색 중...</div>
        <div v-else>
          <div v-if="similarImages.length === 0">유사한 이미지를 찾을 수 없습니다.</div>
          <div v-else style="display:flex; flex-wrap:wrap;">
            <div v-for="img in similarImages" :key="img.productId" style="border:1px solid #ddd; padding:10px; margin:5px; width:150px; text-align:center;">
              <img :src="`http://localhost:8083/images/${img.imageName}`" style="width:100%; height:100px; object-fit:cover;" />
              <div>ID: {{ img.productId }}</div>
              <div>유사도: {{ (img.similarity * 100).toFixed(1) }}%</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import axios from 'axios'

const products = ref([])
const selected = ref([])
const currentView = ref('main')
const compareResult = ref('')
const similarImages = ref([])
const loading = ref(false)

const fetchProducts = async () => {
  try {
    const res = await axios.get('http://localhost:8083/api/products')
    products.value = res.data
  } catch (e) {
    console.error('상품 조회 실패', e)
  }
}

const compare = async () => {
  if (selected.value.length !== 2) {
    alert('상품 두 개를 선택해 주세요')
    return
  }
  currentView.value = 'compare'
  loading.value = true
  try {
    const res = await axios.post('http://localhost:8083/api/compare', {
      pcode1: selected.value[0],
      pcode2: selected.value[1]
    })
    compareResult.value = res.data.analysis
  } catch (e) {
    compareResult.value = '비교 중 오류가 발생했습니다.'
  } finally {
    loading.value = false
  }
}

const searchSimilar = async () => {
  if (selected.value.length !== 1) {
    alert('상품 한 개를 선택해 주세요')
    return
  }
  currentView.value = 'similar'
  loading.value = true
  similarImages.value = []
  try {
    const res = await axios.get('http://localhost:8083/api/similar-images', {
      params: { pcode: selected.value[0], top: 10 }
    })
    if (res.data.success) {
      similarImages.value = res.data.similarImages
    } else {
      alert('유사 이미지 검색 실패: ' + res.data.error)
    }
  } catch (e) {
    alert('유사 이미지 검색 중 오류가 발생했습니다.')
  } finally {
    loading.value = false
  }
}

const goBack = () => {
  currentView.value = 'main'
  compareResult.value = ''
  similarImages.value = []
}

onMounted(fetchProducts)
</script>
